














































































import { Component, Prop, Vue } from "vue-property-decorator";
import { getCurriCulum } from "@/http/curriculum/curriculumManage.ts";
import { getSubjectName } from "@/http/curriculum/subjectMange.ts";
import { gradingList, difficultyDescription } from "@/http/share/share.ts";
import Header from "@/components/Header.vue";
@Component
export default class Login extends Vue {
  private explain = false; // 说明 显示隐藏
  private difficultyDescription = difficultyDescription;
  private difficultyValue = [
    "吓哭级别的难度",
    "吓尿级别的难度",
    "惡夢级别的难度",
    "修罗级别的难度",
    "地獄级别的难度"
  ]; // 难度级别
  private selected = "";
  private curriCulumList = false; // 课程名称按钮显示隐藏
  private levelValue = null; // 等级值
  private subjectValue = null;
  private SubjectNameDataList = []; // 科目数据
  private curriCulumDataList: any[] = []; // 课程数据
  private gradingList = gradingList; // 级别列表数据
  private levelList = false; // 级别按钮显示隐藏
  //  private courseShow = true; //课程列表默认显示
  private total = 0; //列表总条数
  private page = 1; //上拉加载传入页数默认第二页开始
  private size = 10;
  private allLoaded = false; //停止上拉操作，为true停止
  private topStatus = ""; //下拉刷新
  private bottomStatus = ""; //上拉加载
  created() {
    getSubjectName()        // 科目获取
      .then(res => {
        console.log(res);
        this.SubjectNameDataList = res.data.data;
      })
      .catch(error => {
        alert("渲染失败");
      });
    this.courseFuzzy(this.page, this.size, this.subjectValue, this.levelValue);
  }
  private difficulty() {
    this.explain = !this.explain;
  }
  private navCurriCulumList() {
    this.curriCulumList = !this.curriCulumList;
    this.levelList = false;
  }
  private navLevelList() {
    this.levelList = !this.levelList;
    this.curriCulumList = false;
  }
  private kk() {
    this.$router.push({
      path: "/home/welCome"
    });
  }
  // 课程获取
  private courseFuzzy(page: any,size: any,subjectName: any,courseLevel: any) {
    let params = {
      page,
      size,
      subjectName,
      courseLevel
    };
    getCurriCulum(params)
      .then(res => {
        console.log(res);
        let x = res.data.data.records;
        this.total = res.data.data.total;
        this.curriCulumDataList = this.curriCulumDataList.concat(x);
        console.log(this.curriCulumDataList);
        console.log(this.allLoaded)
        if(this.curriCulumDataList.length === this.total){
          this.allLoaded = true;
        }
        console.log(this.allLoaded);
        // (<HTMLDivElement>document.querySelector('.mint-loadmore-content')).style.transform = 'translate3d(0px, 0px, 0px)';
      })
      .catch(error => {
        alert("渲染失败");
      });
  };
  private getCourse(){
    let params = {
      page: this.page,
      size: this.size,
      subjectName: this.subjectValue,
      courseLevel: this.levelValue,
    };
    getCurriCulum(params)
      .then(res => {
        console.log(res);
        // let x = res.data.data.records;
        // this.total = res.data.data.total;
        // this.curriCulumDataList = this.curriCulumDataList.concat(x);
        this.curriCulumDataList = res.data.data.records;
        console.log(this.curriCulumDataList);
        console.log(this.allLoaded);
        if(this.curriCulumDataList.length === this.total){
          this.allLoaded = true;
        }
        console.log(this.allLoaded);
        // (<HTMLDivElement>document.querySelector('.mint-loadmore-content')).style.transform = 'translate3d(0px, 0px, 0px)';
      })
      .catch(error => {
        alert("渲染失败");
      });
  };
  private oop(val: any) {
    console.log(val);
    this.subjectValue = val;
    this.getCourse();
  }
  private getLevel(val: any) {
    this.levelValue = val;
    this.getCourse();
  }
  private loadTop() {
    //下拉刷新触发
    this.page = 1; //恢复上拉加载认页数
    this.curriCulumDataList = [];
    this.allLoaded = false; //回复上拉操作
    this.courseFuzzy(this.page, this.size, this.subjectValue, this.levelValue); //调用课程请求
    setTimeout(()=>{
      this.$refs.loadmore.onTopLoaded();
    },1000)
    console.log(this.$refs.loadmore)  
    console.log(this.$refs.loadmore.onTopLoaded)  
  }
  private loadBottom() {
    //上拉加载数据
    let num = this.total - this.curriCulumDataList.length;
    if(num>0){
      this.page += 1;
    }
    console.log(num)
    this.courseFuzzy(this.page, this.size, this.subjectValue, this.levelValue); //调用课程请求
    this.$refs.loadmore.onBottomLoaded();
  }
  private topChange(status: any) {
    //自定义下拉提示
    console.log(status);
    this.topStatus = status;
  }
  private bottomChange(status: any) {
    //自定义上拉提示
    console.log(status);
    this.bottomStatus = status;
  }
  private goCourse(id: any){
    this.$router.push({
      path: "/CurriculumHourDetails",
      query:{
        id: id,
      },
    })
  };
}
