import axios from '@/http/axios.ts';
import qs from 'qs';
// 数据列表
function getSubject(data: any) {
    return axios.get(`/a/u/allSubject`, {
        params: data,
    });
}
// 查询
function fuzzyQuery(data: any) {
    return axios.get(`/a/u/subjectFuzzy`, {
        params: data,
    });
}
// 查询指定id的课程
function getSubjectIdd(id: number) {
    return axios.get(`/a/u/subject/${id}`);
}
// 修改科目信息
function putSubject(params: any) {
    return axios.put(`/a/u/subject`, qs.stringify(params));
}
// 新增科目类型
function addSubject(params: any) {
    return axios.post('/a/u/subject', params);
}
// 删除
function deleteSubject(id: number) {
    return axios.delete(`/a/u/subject/${id}`);
}
// 上下架
function subjectStatus(id: number) {
    return axios.put(`/a/u/subjectStatus/${id}`);
}
// 科目名称列表
function getSubjectName() {
    return axios.get('/a/u/allSubjectName');
}

export {
    getSubject,
    fuzzyQuery,
    getSubjectIdd,
    putSubject,
    addSubject,
    deleteSubject,
    subjectStatus,
    getSubjectName,
};

